.container {
  padding: 4rem 1rem;

  .title {
    text-align: center;
    span {
      color: #666;
    }
    margin-bottom: 4rem;
  }
  .tabs {
    .tab {
      box-shadow: none;

      .accordionTitle {
        font-size: 1.25rem;
        font-weight: 600;
      }
      .divider {
        width: 100%;
        height: 0.05rem;
        background-color: #c6c6c6;
      }
      .context {
        p {
          font-size: 1.25rem;
          font-weight: 500;
          font-style: italic;
          margin: 0;
        }
        ul {
          padding-left: 1rem;
          li {
            margin: 1rem 0;
            text-align: left;
            font-size: 1rem;
          }
        }
      }
    }
  }
}
